header {
  position: fixed;
  height: 4rem;
  width: 100%;
  background-color: inherit;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: #242526 solid 3px;
  padding: 0 0.8rem;
  top: 0;
  color: white;
  z-index: 1000;
}

header a {
  color: white;
  text-decoration: none;
  top: 12px;
}

.sign-btn {
  background-color: #212324;
  border: none;
  padding: .6rem;
  border-radius: 15px;
  font-size: 1rem;
  color: #ffffff;
  cursor: pointer;
  font-family: 'Asap';
}

.sign-btn:hover,
.sign-btn:active {
  background-color: #495252;
}

.image-wrapper {
  height: 50px;
  width: 50px;
  border-radius: 50px;
  margin-left: 1rem;
  overflow: hidden;
}

.sign-out-container {
  display: flex;
  align-items: center;
}

@media only screen and (min-width: 756px) {
  header {
    padding: 0 2rem;
    height: 5rem;
  }
  .sign-btn {
    padding: 1rem;
  }
}
